:root {
  /* Colores de fondo */
  --bg-dark: #030712;
  --bg-darker: #1a1f24;
  --bg-overlay: rgba(3, 7, 18, 0.8);
  
  /* Colores de acento y gradientes */
  --gradient-primary: linear-gradient(to right, #6366f1, #a855f7, #ec4899);
  --gradient-blue: linear-gradient(to right, #3b82f6, #06b6d4);
  --gradient-green: linear-gradient(to right, #22c55e, #10b981);
  
  /* Colores de texto */
  --text-primary: #ffffff;
  --text-secondary: #94a3b8;
  --text-muted: #475569;
  
  /* Bordes */
  --border-color: #1e293b;
  --border-hover: #3f3f46;
  
  /* Efectos */
  --blur-amount: 8px;
  --transition-duration: 500ms;
  --hover-scale: 1.05;
}

body {
  background-color: var(--bg-dark);
  color: var(--text-primary);
  font-family: system-ui, -apple-system, sans-serif;
}

